@import '../../sassStyles/colors';
@import '../../sassStyles/mixins';

.modal {
  position: fixed; 
  z-index: 1; 
  left: 0%;
  top: 0%;
  width: 100%; 
  height: 100%; 
  background-color: rgba(0,0,0,0.4); 
  
  &__content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 50% auto; 
    
    width: 50%; 
    height: 30%;
    
    border: 1px solid $color__primary;
    
    background-color: $color__main;

    @include onTablet {
      margin: 30% auto;
    }

    @include onDesktop {
      margin: 15% auto;
    }
  }

  &__text {
    position: relative;
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 64px;
    text-align: center;
    color: $color__primary;
  }
}